<template>
  <div class="index-content">
    <el-table
      :data="tableData"
      class="goods-table"
      tooltip-effect="dark"
      fit
      highlight-current-row
      @current-change="handleCurrentChange"
    >
      <el-table-column label="" width="250px">
        <template slot-scope="scope">
          <p class="goods-name">{{scope.row.name}}/{{scope.row.num}}</p>
          <i class="size">{{scope.row.size}}/{{scope.row.company}}</i>
        </template>
      </el-table-column>
      <el-table-column label="/" width="120px">
        <template slot-scope="scope">
          <p>{{scope.row.batchNum}}</p>
          <p>{{scope.row.usageDate}}</p>
        </template>
      </el-table-column>
      <el-table-column label="" prop="inventory"></el-table-column>
      <el-table-column label="" width="100px">
        <template slot-scope="scope">
          <el-input-number
            class="number-input"
            v-model="scope.row.amount"
            size="mini"
            :min="0"
            @change="getChooseChange(scope.row, scope.$index)"
          >
          </el-input-number>
        </template>
      </el-table-column>
      <el-table-column label="" >
        <template slot-scope="scope">
          <p class="salePrice" v-if="scope.row.salePrice">{{scope.row.salePrice}}</p>
          <p>{{scope.row.tagPrice}}</p>
        </template>
      </el-table-column>
      <el-table-column label="" width="100px">
        <template slot-scope="scope">
          <el-input-number
            :controls="false"
            class="number-input"
            v-model="scope.row.actualPrice"
            :min="0"
            size="mini"
            @change="getChooseChange(scope.row, scope.$index)"
          >
          </el-input-number>
        </template>
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <i>{{(+scope.row.actualPrice * +scope.row.amount).toFixed(2)}}</i>
        </template>
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
            <span class="goods-delete" @click="handleDelete(scope.$index)">
              <svg-icon icon-class="delete"/>
            </span>
        </template>
      </el-table-column>
    </el-table>
    <div class="promotion">
      <div class="medicine-img">
        <i class="tip"></i>
        <img :src="promoteInfo.url" :alt="promoteInfo.name" class="img">
      </div>
      <div class="usage">
        <div class="frequency">
          <p></p>
          <i></i>
        </div>
        <div class="time">
          <p></p>
          <i></i>
        </div>
        <div class="date">
          <p></p>
          <i>2022.06</i>
        </div>
      </div>
      <div class="notices">
        <p></p>
        <i>3</i>
      </div>
      <div class="sales">
        <p class="title"></p>
        <div class="sales-card">
          <div class="promote-card">
            <p class="coupons"><i>30</i></p>
            <p></p>
            <p>30020</p>
            <p><i>1~5</i></p>
            <p><i>2019.08.31 ~ 2019.12.31</i></p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import {tableData} from "@/utils/staticData"
    export default {
        name: "index-content",
        data() {
            return {
                promoteInfo: {
                    name: '_',
                    url: 'https://fuss10.elemecdn.com/e/5d/4a731a90594a4af544c0c25941171jpeg.jpeg',
                    frequency: '3',
                    time: '',
                    date: '2022.06',
                    buyAmount: '30',
                    gift: '',
                    application: '',
                    fullReduction: '30020',
                    salesDate: '1~5',
                    salesPeriod: '2019.08.31'
                },
                tableData: tableData,
                chooseId: ''
            }
        },
        methods: {
            getChooseChange(row, index) {
                console.log(row.amount, 'row.amount')
                this.tableData[index] = row
            },
            handleDelete(index) {
                this.$confirm('?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    this.tableData.splice(index, 1)
                }).catch((e) => {
                    // console.error(e);
                });

            },
            handleCurrentChange(val){
              this.chooseId = val.id
            }
        }
    }
</script>

<style lang="scss" scoped>
  @import "~@/style/common/variables.scss";
  .index-content {
    display: flex;
    height: calc( 100vh - 300px);
    .goods-table {
      flex:8;
      margin-right: 10px;
      .goods-delete {
        cursor: pointer;
        width: 10px;
        height: 10px;
      }
      .salePrice{
        color: $menuText;
        text-decoration: line-through;
        font-size: 12px;
        line-height: 16px;
        margin: 0 0;
      }
      .number-input{
        width: 80px;
      }
    }

    .promotion {
      flex:2;
      padding: 0 10px;
      height: 100%;
      overflow-y: auto;
      background-color: #ffffff;

      .medicine-img {
        position: relative;
        width: 100%;
        height: 140px;
        text-align: center;
        padding: 20px 20px;

        .img {
          height: 100%;
          width: 200px;
        }

        .tip {
          position: absolute;
          top: 0;
          right: 0;
          padding: 4px 6px;
          background-color: red;
          color: #ffffff;
          border-radius: 50%;
        }

      }

      .usage {
        display: flex;
        justify-content: space-between;
        line-height: 30px;

        p {
          font-weight: bold;
        }
      }

      .notices {
        p {
          line-height: 30px;
          font-weight: bold;
        }

        i {
          display: -webkit-box;
          -webkit-box-orient: vertical;
          -webkit-line-clamp: 2;
          overflow: hidden;
          line-height: 20px;
        }
      }

      .sales {
        .title {
          line-height: 40px;
          font-weight: bold;
        }

        .promote-card {
          cursor: pointer;
          padding: 10px 10px;
          padding-top: 0;
          background-color: palegreen;
          color: $menuText;

          .coupons {
            line-height: 30px;
          }
        }
      }
    }
  }
</style>



