<template>
  <div class="index-footer">
    <i>ERP <em>V1.0</em></i>
    <i>@</i>
    <i></i>
    <i><em>01</em></i>
    <i>: <em>214123</em></i>
    <i><em>{{isOnline ? '' : ''}}</em></i>
    <i><em></em></i>
    <i>{{date}}</i>
    <i><em></em></i>
  </div>
</template>

<script>
  import {formatTime} from '@/utils/validate'
    export default {
        name: "index-footer",
        data() {
            return {
                isOnline: false,
                timer: '',
                date: formatTime(new Date())
            }
        },
        mounted() {
            clearInterval(this.timer)
            this.timer = setInterval(() => {
                this.isOnline = navigator.onLine
                this.date = formatTime(new Date())
            }, 1000)
        },
        methods: {}
    }
</script>

<style lang="scss" scoped>
  @import "~@/style/common/variables.scss";

  .index-footer {
    margin-top: 10px;
    font-size: 12px;
    line-height: 40px;
    color: $menuText;

    i {
      margin-right: 10px;

      &:last-child {
        margin-right: 0;
      }
    }
  }
</style>



